.particle {
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: -100;
  }

  ::-webkit-scrollbar{
    width:0px;
  }

  ::-webkit-scrollbar-thumb{
    background:rgba(136,136,136,0.281)
  }

  ::-webkit-scrollbar-thumb:hover{
    background:#555;
  }

  .messages {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: none;
  }